let activeUserLink = document.querySelector(".users-nav__link._active")

if(activeUserLink){
    let selectCurrent = document.querySelector(".users-nav__current")
    let usersNav = document.querySelector(".users-nav")

    selectCurrent.innerHTML = activeUserLink.innerHTML

    usersNav.querySelector(".users-nav__select").onclick = function () {
        usersNav.classList.toggle("_active")
    }

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(usersNav);

        if ( ! withinBoundaries ) {
            usersNav.classList.remove("_active")
        }
    })
}

